
.index-header {
  display: none;
}

ul {
  list-style-type: none;
}

@media (min-width: 992px) {
  
  .navbar-brand {
    position: absolute;
    z-index: 1;
  }

  .index-header {
    display: flex;
    position: relative;
    width: 100%;
    height: 30vh;
    object-fit: cover;
  }



}



